\n    &__close {\n      cursor: pointer;\n      display: flex;\n      width: 20px;\n      height: 20px;\n      position: absolute;\n      top: 41px;\n      right: 40px;\n      transition: all 0.3s;\n      border: none;\n      outline: 0;\n      background-color: transparent;\n\n      &:hover {\n        transform: rotate(180deg);\n      }\n\n      &::after {\n        content: \"\";\n        background-image: url(../../../../../public/img/icons/close-icon.svg);\n        background-position: center;\n        background-size: cover;\n        background-repeat: no-repeat;\n        width: 20px;\n        height: 20px;\n        display: flex;\n        justify-content: center;\n        align-items: center;\n      }\n    }\n  }\n\n  &__group {\n    display: flex;\n    flex: 1;\n  }\n\n  &__top {\n    gap: 20px;\n    display: flex;\n    justify-content: space-between;\n  }\n\n  &__bottom {\n    display: flex;\n    margin-bottom: 30px;\n  }\n\n  &__input {\n    border-radius: 30px;\n    position: relative;\n    flex: 1;\n    padding: 15px;\n    border: 1px solid rgba($color: vars.$second-color, $alpha: 0.3);\n\n    &::placeholder {\n      color: vars.$black-color;\n    }\n\n    @include mixins.h4();\n  }\n\n  &__check-input {\n    cursor: pointer;\n    width: 18px;\n    min-width: 18px;\n    height: 18px;\n    border-radius: 3px;\n    border: 1px solid vars.$black-color-second; // Замінив на ваш варіант змінної\n    margin-right: 10px;\n    position: relative;\n    appearance: none; // Прибирає стандартний вигляд\n    -webkit-appearance: none;\n    -moz-appearance: none;\n\n    &:checked {\n      background-color: vars.$second-color; // Використовуємо вашу змінну для активного кольору\n      border-color: vars.$second-color; // Змінюємо колір рамки, якщо треба\n    }\n\n    &:checked::before {\n      content: \"\";\n      position: absolute;\n      display: flex;\n      justify-content: center;\n      align-items: center;\n      top: 4px;\n      left: 4px;\n      width: 8px;\n      height: 8px;\n      background-color: white; // Колір галочки\n      transform: rotate(45deg); // Поворот для створення вигляду галочки\n    }\n  }\n\n  &__check {\n    display: flex;\n    align-items: center;\n    margin-bottom: 20px;\n\n    &__label {\n      position: static !important;\n      @include mixins.h4();\n      color: vars.$black-color-second !important;\n      transform: translate(0) !important;\n    }\n\n    #agree-error {\n      transform: translateY(calc(-100% - 5px));\n    }\n  }\n\n  &__buttons {\n    width: 100%;\n    gap: 20px;\n    display: flex;\n\n    &__button {\n      cursor: pointer;\n      flex: 1;\n      padding: 12px;\n      outline: 0;\n      border: 1px solid vars.$second-color-1;\n      background-color: vars.$second-color-1;\n      color: vars.$white-color;\n      @include mixins.h4();\n      border-radius: 30px;\n      text-align: center;\n      transition: all 0.3s;\n\n      &--back {\n        border: 1px solid vars.$second-color-1;\n        background-color: vars.$white-color;\n        color: vars.$second-color-1;\n        padding: 12px 20px;\n        transition: all 0.3s ease;\n\n        &:hover {\n          background-color: vars.$second-color-1 !important;\n          color: vars.$white-color !important;\n          transform: translateY(-3px);\n        }\n      }\n\n      &:hover {\n        background-color: vars.$white-color;\n        color: vars.$second-color-1;\n        transform: translateY(-3px);\n      }\n    }\n  }\n}\n\n#comment {\n  height: 100px;\n  padding: 15px 15px 50px 15px;\n}\n","$primary-color: #007bff;\n$secondary-color: #6c757d;\n$font-size-base: 16px;\n$second-color: #0b70a2;\n$second-color-1: #06aeef;\n$shadow-color: #47659314;\n$black-color: #3A3A3C;\n$black-color-second: #111111;\n$white-color: #ffff;\n$red-color: #ff214a;\n\n$margin-top-title-option: 10px;\n$padding-vertical-group-inputs: 20px;\n$width-interface: 39vw;\n","@use \"variables\" as vars;\n\n@mixin text-center {\n  text-align: center;\n}\n\n@mixin font-family {\n  font-family: \"Barlow\", serif;\n}\n\n@mixin font-family-second {\n  font-family: \"Staatliches\", serif;\n}\n\n@mixin box-border($blur, $color) {\n  border: 1px solid $color;\n}\n\n@mixin box-shadow($color) {\n  box-shadow: 0px 0px 20px 0px rgba($color, 0.1);\n}\n\n@mixin radius() {\n  border-radius: 10px;\n}\n\n@mixin h2() {\n  font-family: \"Barlow\", serif;\n  font-weight: 400;\n  font-size: 18px;\n  line-height: 100%;\n}\n\n@mixin h3() {\n  font-family: \"Barlow\", serif;\n  font-size: 14px;\n  font-weight: 400;\n  line-height: 21px;\n}\n\n@mixin h4() {\n  font-family: \"Barlow\", serif;\n  font-size: 14px;\n  font-weight: 400;\n  line-height: 21px;\n  text-align: left;\n  text-underline-position: from-font;\n  text-decoration-skip-ink: none;\n}\n\n@mixin h5() {\n  font-family: \"Barlow\", serif;\n  font-size: 10px;\n  font-weight: 400;\n  line-height: 12px;\n  text-align: center;\n  text-underline-position: from-font;\n  text-decoration-skip-ink: none;\n}\n\n@mixin bold {\n  font-family: \"Barlow\", serif;\n  font-size: 12px;\n  font-weight: 500;\n  line-height: 12px;\n}\n"],"sourceRoot":""}]);
