var additionalLightAdded = false;

function addedAdditionalLight(newScene) {
    if (!additionalLightAdded) {
        var dirLight = new DirectionalLight(0xffffff, 2);
        dirLight.position.set(0, 20, 5);
        dirLight.castShadow = true;
        dirLight.shadow.bias = -0.0005;
        dirLight.shadow.radius = 10;
        dirLight.shadow.camera.near = 0.5
        dirLight.shadow.camera.far = 1000
        newScene.add(dirLight);
        additionalLightAdded = true;
    }
}

var objectsInverted = false;
var changeInvert = false;

function ApplyGeometryInvert(object3D) {
    let temp;
    for (let i = 0; i < object3D.geometry.index.array.length; i += 3) {
        temp = object3D.geometry.index.array[i];
        object3D.geometry.index.array[i] = object3D.geometry.index.array[i + 2];
        object3D.geometry.index.array[i + 2] = temp;
    }
}

