var morphedAttributes = null;
var invertObjects = [];

function RefreshGeometry() {
    if (invertObjects.length > 0) {
        for (let index = 0; index < invertObjects.length; index++) {
            const element = invertObjects[index];
            ApplyGeometryInvert(element);
        }

        invertObjects = [];
    }

    objectsInverted = false;
}

function InvertGeometry(object3D) {
    if (object3D.isMesh) {
        ApplyGeometryInvert(object3D);
    }

    if (object3D.children.length > 0) {
        object3D.traverseVisible((groupElement) => {
            if (groupElement.isMesh) {
                ApplyGeometryInvert(groupElement);
                invertObjects.push(groupElement);
            }
        });
    }
}

function InvertGeometryBack(object3D) {
    if (object3D.scale.x < 0 || object3D.scale.y < 0 || object3D.scale.z < 0) {
        InvertGeometry(object3D);
        objectsInverted = true;
    }
}

