gn-items: center;\n  flex-direction: column;\n\n  &__title {\n    @include mixins.font-family-second();\n    font-weight: 400;\n    font-size: 20px;\n    line-height: 100%;\n    letter-spacing: 0%;\n    vertical-align: bottom;\n    text-transform: uppercase;\n    color: vars.$second-color;\n    margin-bottom: 24px;\n  }\n\n  &__close {\n    cursor: pointer;\n    display: flex;\n    width: 18px;\n    height: 18px;\n    position: absolute;\n    top: 40px;\n    right: 21px;\n    transition: all 0.3s;\n\n    &:hover {\n      transform: rotate(180deg);\n    }\n\n    &::after {\n      content: \"\";\n      background-image: url(../../../../public/img/icons/close-icon.svg);\n      background-position: center;\n      background-repeat: no-repeat;\n      width: 20px;\n      height: 20px;\n      display: flex;\n      justify-content: center;\n      align-items: center;\n    }\n  }\n\n  &__main-list {\n    width: 100%;\n    list-style: none;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    flex-direction: column;\n    gap: 14px;\n\n    &__title {\n      text-align: center;\n      @include mixins.font-family-second();\n      font-weight: 400;\n      font-size: 14px;\n      line-height: 100%;\n      letter-spacing: 0%;\n      text-transform: uppercase;\n      border-bottom: 0.5px solid rgba($color: vars.$second-color, $alpha: 0.3);\n      padding-bottom: 10px;\n      margin-bottom: 10px;\n      color: vars.$second-color;\n    }\n\n    &__info {\n      width: 100%;\n      display: flex;\n      justify-content: space-between;\n      margin-bottom: 5px;\n\n      &__title {\n        @include mixins.font-family();\n        font-weight: 400;\n        font-size: 14px;\n        line-height: 120%;\n        letter-spacing: 0%;\n\n        &--el {\n          padding: 20px 0;\n        }\n\n        // &--lil {\n        //   @include mixins.h4();\n        // }\n      }\n\n      &__param {\n        @include mixins.font-family();\n        font-weight: 400;\n      